import axios from 'axios'
// å¼•å…¥æç¤ºæ¡†(ä»¥å®é™…é¡¹ç›®ä¸ºå‡†)
import { Message } from "element-ui";
// å¼•å…¥è·¯ç”±(ä»¥å®é™…é¡¹ç›®ä¸ºå‡†)
import router from '@/router'
//å¼•å…¥loadingç»„ä»¶(ä»¥å®é™…é¡¹ç›®ä¸ºå‡†)
import { Loading } from 'element-ui';
// åˆ›å»ºçš„axiosçš„å®åˆ— requests
const requests = axios.create({
    baseURL:"http://localhost:8080", // åŸºå‡†åœ°å€  å¯ç›´æ¥é…ç½®è·¯å¾„,ä¹Ÿå¯ä»£ç†è·¯å¾„(ä»¥å®é™…é¡¹ç›®ä¸ºå‡†)
    timeout: 10000 // è¶…æ—¶æ—¶é—´
})
// è¯·æ±‚æ‹¦æˆª
// å£°æ˜å˜é‡æ¥æ”¶logding
let loadingInstance
requests.interceptors.request.use((config) => {
    // // è®¾ç½®è¯·æ±‚å¤´
    // let token = localStorage.getItem('token')
    // config.headers.Authorization = token
    // è®¾ç½®logdingé…ç½® èµ‹å€¼
    loadingInstance = Loading.service({
        lock: true,
        text: 'æ‹¼å‘½åŠ è½½ä¸­...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
    });
    // è¿”å›è¯·æ±‚å¤´
    return config
}, err => {
   // è¯·æ±‚å¤±è´¥è¿”å›å¤±è´¥ä¿¡æ¯
    return Promise.reject(err)
})

// å“åº”æ‹¦æˆª
requests.interceptors.response.use((res) => {
    // å“åº”æˆåŠŸåˆ™å…³é—­loadingåŠ è½½
    loadingInstance.close();
    // è¿”å›å“åº”æ•°æ®
    return res
}, err => {
    // å“åº”å¤±è´¥åˆ™å…³é—­loadingåŠ è½½  
    setTimeout(() => {
        loadingInstance.close();
    }, 1000);
    // å“åº”å¤±è´¥è¿”å›å¤±è´¥ä¿¡æ¯
    return Promise.reject(err)
})

export default requests